$desatRed: hsl(0, 36%, 70%);
$softRed: hsl(0, 93%, 68%);
$darkGreyishRed: hsl(0, 6%, 24%);

$lightGrey: hsl(0, 16%, 83%);
$veryLightGreen: hsl(58, 83%, 84%);
$lightGreen: hsl(359, 86%, 78%);


body{
    margin: 0px;
}

#logo{
    height: 20px;
    margin: 33px auto auto 32px;
}

#hero-mobile{
    margin-top: 27px;
    width: 100%;
}

#hero-desktop{
    display: none;
}


main{
    text-align: center;
}


h1{
    font-family: 'Josefin Sans', sans-serif;
    text-transform:uppercase;
    font-size: 40px;
    letter-spacing: 10px;
    line-height: 42px;
}

#tagline-a{
    font-weight: 300;
    color: $desatRed;
    margin: 58px 0px 0px 0px;
    letter-spacing: 11px;
}

#tagline-b{
    font-weight: 600;
    color: $darkGreyishRed;
    margin: 0px 0px 0px 0px;
}

p{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 14.5px;
    color: $desatRed;
    margin: 16px 30px 0px 30px;
    line-height: 22px;
}

#email-input-section{
    margin: 32px 31px auto 33px;
    position: relative;

    label{
        display: none;
    }
}

#email-input{
    width: 100%;
    height: 48px;
    box-sizing: border-box;

    font-family: 'Josefin Sans', sans-serif;
    font-weight: 600;
    padding-left: 24px;
    padding-top: 4px;
    color: $lightGrey;

    border-radius: 25px;
    border: 1px solid;
    border-color: $lightGrey;

}

#submit-btn{
    position: absolute;
    top: 0px;
    right: 0px;

    height: 48px;
    width: 65px;
    border:none;
    border-radius: 25px;

    background: linear-gradient(100deg, $veryLightGreen, $lightGreen);
}


footer{
    position: absolute;
    right: 0px;
    bottom: 0px;

    p{
        font-size: 10px;
    }
}


//Desktop view
@media only screen and (min-width = 1000px){
    body{
        display: grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: 100%;
    }

    #hero-mobile{
        display: none;
    }
    
    #text-section{

        text-align: left;
        grid-column: 1;
        grid-row: 1;
        width: 50%;

        margin: auto;
    }

    #hero-desktop{
        display: block;
        grid-column: 2;
                grid-row: 1;
        width: 100%;
    }

    #logo{
        position: absolute;
        top: 67px;
        left: 117px;
        height: 32px;
    }




}

    
